import React from 'react';
import { useTheme } from '@material-ui/core/styles';
// import Title from './Title';
import {   PieChart, Pie, ResponsiveContainer } from 'recharts';

// TITLE START
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';

function Title(props) {
  return (
    <Typography component="h2" variant="h6" color="primary" gutterBottom>
      {props.children}
    </Typography>
  );
}

Title.propTypes = {
  children: PropTypes.node,
};
// TITLE END

const data01 = [
  { name: 'Group A', value: 400 }, 
  { name: 'Group B', value: 300 },
];
const data02 = [
  { name: 'A1', value: 100 },
  { name: 'A2', value: 300 },
  { name: 'B1', value: 100 },
  { name: 'B2', value: 80 },
];

export default function Chart() {
  const theme = useTheme();

  return (
    <>
      <Title>Market Segment</Title>
      <ResponsiveContainer>
      <PieChart width={200} height={200}>
        <Pie data={data01} dataKey="value" cx={'50%'} cy={'50%'} outerRadius={60} fill="#8884d8" />
        <Pie data={data02} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={70} outerRadius={90} fill="#82ca9d" label />
        <Pie data={data01} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={100} outerRadius={120} fill="#8884d8" label />
        <Pie data={data02} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={130} outerRadius={150} fill="#82ca9d" label />
        <Pie data={data01} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={160} outerRadius={180} fill="#8884d8" label />
        <Pie data={data02} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={190} outerRadius={210} fill="#82ca9d" label />
        <Pie data={data01} dataKey="value" cx={'50%'} cy={'50%'} innerRadius={220} outerRadius={240} fill="#8884d8" label />
      </PieChart>
      </ResponsiveContainer>
    </>
  );
}



